import React from 'react';

// Material UI Components
import {Button} from '@material-ui/core';

// Icons
import FiberManualRecordRoundedIcon from '@material-ui/icons/FiberManualRecordRounded';

export default function FontColor( {StyleButton, currentSelectionOffset, contentBlock, onClickToggleStyle} ) {
    const fontColors = ["INHERIT", "#FAFAFA", "#BDC3C7", "#C0392B", "#F1C40F", "#F39C12", "#8E44AD", "#2980B9", "#27AE60"];
    const stylesArray = [...contentBlock.getInlineStyleAt(currentSelectionOffset)]

    let [fontColorActive, setFontColorActive] = React.useState('INHERIT');

    React.useEffect(() => {
        fontColors.forEach(font => {
            const fontColor = stylesArray.find(style => style === font) || "";
            
            switch(fontColor){
                case "INHERIT":
                    setFontColorActive("INHERIT");
                    break;
                case "#FAFAFA":
                    setFontColorActive("#FAFAFA");
                    break;
                case "#BDC3C7":
                    setFontColorActive("#BDC3C7");
                    break;
                case "#C0392B":
                    setFontColorActive("#C0392B");
                    break;
                case "#F1C40F":
                    setFontColorActive("#F1C40F");
                    break;
                case "#F39C12":
                    setFontColorActive("#F39C12");
                    break;
                case "#8E44AD":
                    setFontColorActive("#8E44AD");
                    break;
                case "#2980B9":
                    setFontColorActive("#2980B9");
                    break;
                case "#27AE60":
                    setFontColorActive("#27AE60");
                    break;
                default:
                    break;
            }
        });
    }, [fontColors, stylesArray])

    return (
        <React.Fragment>
            {
                fontColors.map((item, index) => {
                    const checkItem = item === "INHERIT" ? "#000000" : item 
                    return(

                        <div key={index} onMouseDown={(event) => {
                            event.preventDefault();
                            onClickToggleStyle(item)
            
                        }}>
                            <Button 
                                aria-label={item === "INHERIT" ? `font color hex: #000000` : `font color hex: #${item}`} 
                                className={StyleButton} 
                                color={(fontColorActive === item) ? "primary" : "inherit"} 
                                size="small"
                                style={{maxWidth: "34px", minWidth: "0", margin: "0"}}
                                variant={(fontColorActive === item) ? "contained" : "text"}>
                                    <FiberManualRecordRoundedIcon style={{color: checkItem}} />
                            </Button  >
                        </div>
                    );
                })
            }
        </React.Fragment>
    )
}